*,
*::after,
*::before {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  user-select: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  color: var(--text);
}

::placeholder {
  opacity: .6;
  transition: opacity .2s;
}

:focus::placeholder {
  opacity: 0;
}

button.ui-button {
  padding: 3px 14px;
  color: var(--button-color);
  background: var(--button);
  border: 1px solid var(--button-border);
  background-clip: border-box;
  font-size: 13px;
  border-radius: 5px;
}

button.ui-button:focus {
  color: #fff;
  background: var(--accent);
  background-image: linear-gradient(var(--accent-light), var(--accent));
  border: 1px solid var(--accent);
  outline: none;
}

button.ui-button:active {
  color: #fff;
  background: var(--accent-dark);
  background-image: linear-gradient(var(--accent), var(--accent-dark));
  border: 1px solid var(--accent-dark);
}

button.ui-button:disabled {
  opacity: .5;
}

header {
  padding: 10px 40px;
  background-color: var(--app-light);
  font-size: 13px;
  line-height: 18px;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.scroll {
  position: fixed;
  top: 38px;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: scroll;
}

form {
  padding: 20px 40px 0;
}

form ul {
  list-style-type: none;
}

form ul li {
  padding-bottom: 10px;
}

form ul li label {
  display: block;
  float: left;
  width: 80px;
  text-align: right;
  padding-right: 10px;
  font-size: 13px;
  line-height: 28px;
}

form ul li label::after {
  content: ':';
}

form ul li input {
  display: block;
  padding-left: 10px;
  padding-right: 28px;
  width: calc(100% - 80px);
  color: var(--input-color);
  background-color: var(--input-background);
  border: 1px solid var(--input-border);
  background-position: right 6px top 6px;
  background-repeat: no-repeat;
  transition: background-color .15s;
}

form ul li input[required]:invalid:not(:placeholder-shown) {
  background-image: var(--error);
}

form ul li input[required]:valid {
  background-image: var(--check);
}

form ul li input {
  height: 28px;
}

footer {
  text-align: right;
  padding: 15px 0 0;
}

[data-theme="dark"] header {
  color: #fff;
  background-color: var(--app-dark);
}

[data-user="light"] body {
  background-color: #F0F0F0;
}

[data-user="dark"] body {
  background-color: #383838;
}
